import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { ListViewWebElement } from './ListViewWebElement';
export { ListViewWebElement };

/**
 * Retrieve an instance of [[ListViewWebElement]].
 * @example
 * ```javascript
 * import { findListView } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findListView(driver, By.id('my-oj-c-list-view'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findListView(driver: DriverLike, by: By): Promise<ListViewWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type ListViewWebElement
  if (!(webEl instanceof ListViewWebElement)) {
    const tagName = await webEl.getTagName();
    throw Error(
      `findListView(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${ListViewWebElement.name}`
    );
  }
  return webEl;
}

register('oj-c-list-view', ListViewWebElement);
